.game-container {
	width: 100%;
	height: -webkit-fill-available;
	height: -moz-available;
	height: stretch;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}

.back-btn-container {
	position: fixed;
	top: 25px;
	left: 25px;
	cursor: pointer;
}

.back-btn-image {
	width: 100px;
}

.music-container {
	position: fixed;
	top: 25px;
	right: 25px;
	cursor: pointer;
	overflow: hidden;
}

.music-image {
	width: 100px;
}

.cross-music-image {
	position: absolute;
	-webkit-transform: rotate(45deg);
	    -ms-transform: rotate(45deg);
	        transform: rotate(45deg);
	width: 200px;
	top: -50px;
	left: -35px;
}



