.spinner-container {
	position: fixed;
	width: 100%;
	height: -webkit-fill-available;
	height: -moz-available;
	height: stretch;
	background-color: #ccccccbb;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}

